body{
  background-color:#faa916;
}

.App{
  background-color: #1b1b1e;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  padding-top: 1px;
}

#to-do-form input{
  background-color:#fbfffe;
  border: red;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #1b1b1e;
}

#to-do-form::placeholder{
  background-color: #1b1b1e;
}

#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  background-color: #6d676e;
  cursor: pointer;
  font-weight: bolder;
  color:#fbfffe;
}

#myTitle {
  background-color: #0e8878;
  position: relative;
  border: red;
  width: 200px;
  height: 40px;
  padding: 0px 5px;
  margin: auto;
  margin-top: 20px;
  font-size: 24px;
  border-radius: 10px;
  color: #fbfffe;
}
